import $ from 'jquery';
import {zoomable} from './zoomable';
import tpl_profile_information from 'resources/tpl/profile-information.tpl.html';
import tpl_profile_information_item from 'resources/tpl/profile-information-item.tpl.html';
import {gnb} from './gnb';

var $body = $('content.body .contents');

var navigate = function(target) {
    if(typeof NAVIGATION[target] ==='function' ){
        $body.empty();
        $body.attr('data-navigation', target);
        $(document.body).attr('data-navigation', target);
        location.href='#!/'+target;
        NAVIGATION[target]();
        $('.gnb li').removeClass('hover on');
        $('.gnb li[data-key="'+target+'"]').addClass('hover on');
        $(window).scrollTop(0)
        gnb.resetIndicator();
    } 
}

var NAVIGATION = {
    elearning: function(){
        var $title = $('content.body .title');
        $title.html('E-LEARNING DESIGN');
        var $list = $('<ul class="box-list"></ul>');
        $.ajax({
            url: 'resources/data/elearning.json',
            dataType: 'json',
            success: function(result) {
                console.log(result);
                processResult($list, result);
            }
        })
        var $container = $('<div class="container"></div>');
        $container.append($list);
        $body.append($container);
        
    },
    web: function() {
        var $title = $('content.body .title');
        $title.html('WEB DESIGN');

        var $list = $('<ul class="box-list"></ul>');
        $.ajax({
            url: 'resources/data/web.json',
            dataType: 'json',
            success: function(result) {
                try{
                    console.log(result);
                    processResult($list, result);
                }catch(e) {
                    console.error(e)
                }
            }
        })

        var $container = $('<div class="container"></div>');
        $container.append($list);
        $body.append($container);
    },
    illust: function() {
        var $title = $('content.body .title');
        $title.html('ILLUSTRATION');

        var $list = $('<ul class="box-list"></ul>');
        $.ajax({
            url: 'resources/data/illust.json',
            dataType: 'json',
            success: function(result) {
                try{
                    console.log(result);
                    processResult($list, result);
                }catch(e) {
                    console.error(e)
                }
            }
        })
        var $container = $('<div class="container"></div>');
        $container.append($list);
        $body.append($container);
    },
    profile: function() {
        var $title = $('content.body .title');
        $title.html('PROFILE');
        var template_profile = tpl_profile_information;
        var template_profile_item = tpl_profile_information_item;
        $body.append(template_profile);
        var $list = $body.find('.profile-items div.list');
        $.ajax({
            url: 'resources/data/profile.json',
            dataType: 'json',
            success: function(result) {
                try{
                    console.log(result);

                    var items = result.items;
                    for(var i=0;i<items.length;i++) {
                        var _item = items[i];
                        console.log(_item)
                        var $item = $(template_profile_item);
                        $('.duration', $item).html(_item.duration)
                        $('.work-at', $item).html(_item.title)
                        $('.desc', $item).html(_item.desc)
                        $list.append($item);
                    }

                }catch(e) {
                    console.error(e)
                }
            }
        })
    }

};
var processResult = function($list, result) {
    var items = result.items;
    if(result.reverseOrder) {
        items.reverse();
    }
    for(var i=0;i<items.length;i++) {
        var _item = items[i];
        console.log(_item)
        var $item = $('<li class="item"></li>');
        var thumbnail = _item.thumbnail || _item.images[0];
        var $inner = zoomable({
            href: 'resources/images/'+thumbnail,
            scale: _item.scale || result.base_scale || 1,
            title: _item.title,
            description: _item.desc,
            data: _item,
            zoomable: !!result.zoomable,
            zoomtype: result.zoomtype || 'normal'
        });
        $item.append($inner);

        $list.append($item);
    }
}

export {navigate};