import ScrollMagic from 'scrollmagic';
import $ from 'jquery';
import {navigate} from './navigate';
import {gnb} from './gnb';

const initScroll = function() {
    const controller = new ScrollMagic.Controller();
    new ScrollMagic.Scene({
        //triggerElement: '#trigger',
        offset: 50,
        duration: 100
    })
    //.addIndicators({name: 'test indicator'})
    .addTo(controller);

    new ScrollMagic.Scene({
        offset: 250
    })
    .setClassToggle('header','gnb-attached')
    .on('enter leave', function(e) {
        console.log(e.type)
        setTimeout(function() {
            gnb.resetIndicator();
        }, 350)
    })
    //.addIndicators()
    .addTo(controller);

}



const init = function() {
    initScroll();
    gnb.init();
    $('button.btn-mail').on('click', function() {
        location.href='mailto:syjj84@nate.com';
    });
    $('button.btn-home').on('click', function() {
        navigate('profile');
    })

}

$(function() {
    $(document.body).removeClass('loading');
    init();
    navigate('profile');
});
