import $ from 'jquery';

const setImageMargin = function($img) {
    var img = $img.get(0);
    var nw = img.naturalWidth;
    var nh = img.naturalHeight;

    if(nw>1024) {
        nh = 1024 * nh / nw;
        nw = 1024;
    }
    $img.css({
        marginLeft: nw/-2,
        marginTop: nh/-2
    });
}
const setCenterPosition = function($img) {
    if(!$img.get(0).naturalWidth) {
        $img.on('load', function(e) {
            setImageMargin($img)
        })
    } else {
        setImageMargin($img)
    }
}
var zoomable = function(cfg) {
    console.log(cfg)
    var $wrapper = $('<div class="zoomable loading"></div>');
    var $inner = $('<div class="img-wrap"/>');
    var $img = $('<img class="inner"/>');
    var $button = $('<button class="zoom-in"></button>');
    $img.css({
        transform:'scale('+cfg.scale+')'
    })
    $img.on('load', function(e) {
        var w = $wrapper.width();
        var h = $wrapper.height();
        var img = e.target;
        //console.log(w,h,img.naturalWidth, img.naturalHeight)

        $(img).css({
            left: -(img.naturalWidth - w)/2,
            top: -(img.naturalHeight - h)/2
        })

    });
    
    var showContentOnMask = (function(content) {
        return function() {
            var $contentOnMask = $('body .mask .content');
            $contentOnMask.removeClass('single-big');
            $contentOnMask.empty();
            if(cfg.zoomtype==='single-big') {
                $contentOnMask.addClass('single-big');
            }
            console.log(content.images)
            

            if(content.images && content.images.length) {
                var $contents = $('<ul class="content-list"></ul>')
                for(var i=0;i<content.images.length;i++) {

                    var image = content.images[i];
                    console.log(image)
                    var $item = $('<li class="item"></li>');
                    var $img = $('<img/>');
                    
                    $img.attr('src', 'resources/images/'+image);
                    setCenterPosition($img);

                    $item.append($img);
                    $contents.append($item);
                }


                $contents.children().first().addClass('current');

                var $prev = $('<button class="nav prev"> </button>');
                var $next = $('<button class="nav next"> </button>');

                $contentOnMask.append($prev);
                $contentOnMask.append($next);

                var ALL_CONTENT_COUNT = content.images.length;
                var resetPrevNext = function() {
                    var currentIndex = $('.content-list li').index($('.content-list .current'));
                    if(currentIndex === 0 ) {
                        $prev.attr('disabled','disabled');
                    } else {
                        $prev.removeAttr('disabled')
                    }
                    if(currentIndex === ALL_CONTENT_COUNT -1 ) {
                        $next.attr('disabled','disabled');
                    } else {
                        $next.removeAttr('disabled')
                    }

                }
                $prev.off('click').on('click', function() {
                    var current = $('.content-list li').index($('.content-list .current'));
                    if(current>0) {
                        current--;
                    }
                    $('.content-list li').removeClass('current');
                    $('.content-list li').eq(current).addClass('current');

                    var $img = $('.content-list li.current img');
                    setCenterPosition($img);

                    resetPrevNext();
                });
                $next.off('click').on('click', function() {
                    var current = $('.content-list li').index($('.content-list .current'));
                    if(current<ALL_CONTENT_COUNT-1) {
                        current++;
                    }
                    $('.content-list li').removeClass('current');
                    $('.content-list li').eq(current).addClass('current');

                    var $img = $('.content-list li.current img');
                    setCenterPosition($img);

                    resetPrevNext();

                });
                //$prev.click();
            }
            $contentOnMask.append($contents)
            resetPrevNext();
        }
    })(cfg.data);

    $button.on('click', function() {
        $('body').addClass('masked');
        $('body .mask button.close').off('click').on('click', function() {
            $('body').removeClass('masked');
        });
        showContentOnMask();
    });

    $inner.append($img)
    $wrapper.append ($inner);
    if(cfg.title) {
        var $content = $('<div class="content-title"></div>');
        var $title = $('<h5></h5>');
        $title.html(cfg.title);
        $content.append($title);
        if(cfg.description) {
            var $desc = $('<p></p>');
            $desc.html(cfg.description);
            $content.append($desc);
        }
        $wrapper.append($content);
        $wrapper.addClass('with-title')
    }
    if(cfg.zoomable) {
        $wrapper.append($button)
    }
    $img.attr('src',cfg.href);

    return $wrapper;
}

export {
  zoomable
};