import $ from 'jquery';
import {navigate} from './navigate';

var init = function() {
  $('section.gnb').on('mouseenter','li', function(e) {
      $('section.gnb ul li').removeClass('hover');
      var $li = $(this);
      $li.addClass('hover');
      if($li.hasClass('on')) {
        $('section.gnb ul').removeClass('other-focused');
      } else {
        $('section.gnb ul').addClass('other-focused');
      }
      resetIndicator();
  });
  $('section.gnb').on('click','li', function(e) {
    $('section.gnb ul').removeClass('other-focused');
      var $li = $(this);
      $li.addClass('on');
      var key = $li.attr('data-key');
      navigate(key);
  });

  $('section.gnb').on('mouseleave',function(e) {
    $('section.gnb ul').removeClass('other-focused');
    $('section.gnb ul li').removeClass('hover');
    resetIndicator();
  });
}

var resetIndicator = function() {
  setTimeout(function(){
      var $li = $('section.gnb li.hover');
      var $indicator = $('section.gnb .gnb-indicator');
      if(!$li.length) {
          $li = $('section.gnb li.on');
      }
      if($li.length) {
          console.log($li.width())
          $indicator.css({
              width: $li.width() + 40,
              left: $li.get(0).offsetLeft+ 30,
              top: 42
          });
      }
  }, 100);
}
const gnb = {
  init,
  resetIndicator
}

export {gnb};